import {uniRate} from "@dcloudio/uni-ui"
export default {
	components:{
		uniRate
	},
	data() {
		return {
			swiper:[],//轮播图数据
			superhero:[],//热门超英
			trailer:[],//热门预告
			like:[],//猜你喜欢
		}
	},
	onLoad() {
		this.getSwiper();
		this.getSuperhero();
		this.getLike();
		this.getTrailer();
	},
	methods: {
		toMovieDetail(id){
			uni.navigateTo({
				url: '../../pages/detail/detail?id='+id,
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		},
		timeFormat(time){//格式化时间
			var date=new Date(time);
			var year=date.getFullYear();
			var month= date.getMonth()+1<10 ? "0"+(date.getMonth()+1) : date.getMonth()+1;
			var day=date.getDate()<10 ? "0"+date.getDate() : date.getDate();
			// 拼接
			return year+"-"+month+"-"+day;
		},
		getSwiper(){//获得轮播图数据
			uni.request({
				url: `${this.$url}/index/carousel/list?qq=929029017`,
				method: 'POST',
				data: {},
				success: res => {
					this.swiper = res.data.data;
					console.log(res.data)
				},
				fail: () => {},
				complete: () => {}
			});
		},
		getSuperhero(){//获得热门超英
			uni.request({
				url: `${this.$url}/index/movie/hot?qq=40699904&&type=superhero`,
				method: 'POST',
				data: {},
				success: res => {
					this.superhero = res.data.data;
					console.log(res.data);
				},
				fail: () => {},
				complete: () => {}
			});
		},
		getTrailer(){//热门预告
			uni.request({
				url: `${this.$url}/index/movie/hot?qq=40699904&&type=trailer`,
				method: 'POST',
				data: {},
				success: res => {
					this.trailer = res.data.data;
					console.log(res.data);
				},
				fail: () => {},
				complete: () => {}
			});
		},
		getLike(){//猜你喜欢
			uni.request({
				url: `${this.$url}/index/guessULike?qq=40699904`,
				method: 'POST',
				data: {},
				success: res => {
					this.like = res.data.data;
					console.log(res.data);
				},
				fail: () => {},
				complete: () => {}
			});
		},
	}
}
